.profile{
  max-width: 880px;
  margin: 0 auto;
  display: flex;
}
@media screen and (min-width: 681px) and (max-width:900px) {
  .profile{
    max-width: 651px;
  }
}
@media screen and (min-width: 320px) and (max-width:681px) {
  .profile{
    min-width: 282px;
    flex-direction: column;
  }
}